/**
 * Branding styles
 *  - Drupal core branding block.
 ============================================================================ */
.block--system-branding-block {
  margin: 1.25rem 0;
}

.site-branding {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  padding-top: 1rem;
}

.site-branding__text, .site-branding__name, .site-branding__slogan {
  display: block;
}

.site-branding__logo-link {
  display: block;
  padding-right: 1rem;
}

.site-branding__text {
  display: block;
  margin: 0;
}

.site-branding__name {
  font-size: 1.75rem;
  padding-right: 1rem;
  font-weight: 700;
}

@media all and (min-width: 60.063em) {
  .site-branding__name {
    font-size: 2.25rem;
  }
}

.site-branding__name__word--at {
  margin-right: -0.2em;
}

.site-branding__name-link:hover, .site-branding__name-link:focus {
  text-decoration: none;
}

.site-branding__slogan {
  font-style: normal;
  font-size: 1.25rem;
  padding: 0.75rem 0 0;
}

